<template>
  <div>
    <AlertDefault
      v-if="alert.alive"
      :message="alert.message"
    />
    <form
      v-if="!send"
      @submit.prevent="resetPassword"
    >
      <div>Пароль:</div>
      <input id="password" v-model="password" />
      <div>Новый пароль:</div>
      <input id="password_confirmation" v-model="password_confirmation" />
      <button>Восстановить пароль</button>
    </form>

  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'PasswordReset',
  data () {
    return {
      email: '',
      password: '',
      password_confirmation: '',
      send: false,
      alert: {
        alive: false,
        message: ''
      }
    }
  },
  props: {
    password_code: {
      type: String,
      required: true
    }
  },
  methods: {
    resetPassword () {
      if (this.password === this.password_confirmation) {
        var data = JSON.stringify({

        })
        const alert = this.alert
        axios.post('http://172.100.2.15:8000/password/reset', data)
          .then(response => {
            alert.message = response.data.message
            alert.alive = true
            this.send = true
          })
          .catch(error => {
            // let message = error.Error.message
            // this.alertMessage = message
            console.info(error)
            alert.message = 'Произошла ошибка'
            alert.alive = true
          })
        setTimeout(function () {
          alert.alive = false
        }, 3000)
      }
    }
  }
}
</script>

<style scoped>

</style>



